:root {
  color-scheme: light;
}

* {
  box-sizing: border-box;
}

html, body {
  padding: 0;
  margin: 0;
  font-family: "Inter", sans-serif;
  background-color: #f5f5f5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: min(100% - 2rem, 1200px);
  margin-inline: auto;
}

.pad {
  padding-block: 3rem;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

@media (max-width: 900px) {
  .grid-2, .grid-3 {
    grid-template-columns: 1fr;
  }
}
.site-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: transparent;
  border: none;
}

.secondary {
  position: relative;
  background-color: #1f1529;
}

.brand {
  max-width: 50%;
}

.brand img {
  max-width: 35%;
}

.site-header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 72px;
  gap: 1rem;
  border-block-end: 1px solid #ffffff;
}

.brand--logo {
  text-decoration: none;
  color: inherit;
}

.brand__text {
  font-weight: 900;
  letter-spacing: 0.3px;
  font-size: 1.25rem;
  color: #0f172a;
}

.brand__tag {
  color: #64748b;
  font-weight: 400;
  margin-left: 0.5rem;
}

.menu {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* === Enlaces normales (sin clase) === */
.menu a:not(.btn) {
  position: relative;
  text-decoration: none;
  color: #ffffff;
  padding: 0.5rem 0.8rem;
  transition: transform 0.3s ease-in-out;
}

.menu a:not(.btn)::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0%;
  background: #005AFF;
  transition: width 0.3s ease-in-out;
}

.menu a:not(.btn):hover {
  transform: translateX(4px);
}

.menu a:not(.btn):hover::after {
  width: 100%;
}

/* ===  Botón especial (.btn) === */
.menu .btn {
  position: relative;
  background-color: #005AFF;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  transition: background 0.3s ease-in-out, transform 0.3s ease-in-out;
  color: #ffffff;
  text-decoration: none;
}

/* Efecto distinto para el botón: brillo al pasar */
.menu .btn::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  transition: left 0.4s ease-in-out;
  border-radius: inherit;
}

.menu .btn:hover {
  background-color: #1f1529;
  transform: scale(1.05);
}

.menu .btn:hover::before {
  left: 100%; /* brillo pasa de izquierda a derecha */
}

.nav-toggle {
  display: none;
  color: #ffffff;
}

@media (max-width: 900px) {
  .nav-toggle {
    display: block;
    background: none;
    border: 1px solid #ccc;
    padding: 0.4rem 0.6rem;
    border-radius: 8px;
    cursor: pointer;
    z-index: 60;
  }
  .menu {
    position: fixed;
    left: 0;
    right: 0;
    top: 72px;
    background: #fff;
    padding: 1rem 1.25rem 1.5rem;
    border-bottom: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
    z-index: 55;
    /* cerrado */
    transform: translateY(-16px);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: transform 0.25s ease, opacity 0.2s ease, visibility 0.2s ease;
  }
  .menu.open {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  /* En móvil, texto oscuro sobre fondo blanco */
  .menu a:not(.btn) {
    color: #111827;
    width: 100%;
  }
  .menu .btn {
    align-self: stretch;
    text-align: center;
  }
}
@media (max-width: 900px) {
  /* 1) evita scroll horizontal accidental */
  html, body {
    overflow-x: hidden;
  }
  .menu {
    position: fixed;
    top: 72px;
    left: 0;
    right: 0;
    /* 2) importante: que el padding NO sume ancho */
    box-sizing: border-box;
    /* 3) por si algún hijo se desborda */
    overflow-x: hidden;
    background: #fff;
    padding: 1rem 1.25rem 1.5rem;
    border-bottom: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
    z-index: 55;
    /* cerrado */
    transform: translateY(-16px);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: transform 0.25s ease, opacity 0.2s ease, visibility 0.2s ease;
  }
  .menu.open {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  /* 4) fuerza a que los links no “empujen” el ancho */
  .menu a {
    display: block;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    overflow-wrap: anywhere; /* si por error hay texto largo */
  }
  .menu .btn {
    width: 100%;
    max-width: 100%;
  }
}
.card {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 1.25rem;
}

.site-footer {
  margin-top: 3rem;
  background: #0f0f11;
  color: #ddd;
  padding-block: 2rem;
}

.site-footer .brand {
  color: #ffffff;
  font-weight: 900;
}

.site-footer h4 {
  margin: 0 0 0.5rem;
  color: #ffffff;
}

.site-footer .list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 0.4rem;
}

.site-footer a {
  color: #ddd;
  text-decoration: none;
}

.copy {
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 1rem;
  padding-top: 1rem;
  font-size: 0.9rem;
  color: #aaa;
}

/* ===== Footer visual sobre el mismo fondo azul ===== */
.site-tail {
  position: relative;
  overflow: hidden;
  padding-block: clamp(0.5rem, 1vw, 0.5rem);
  color: #ffffff;
  background: radial-gradient(1200px 600px at 25% -10%, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0) 60%), radial-gradient(900px 500px at 80% 30%, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0) 60%), linear-gradient(90deg, #0b3e74, #052b52);
}

.site-tail__row {
  display: grid;
  grid-template-columns: 1fr auto auto;
  align-items: center;
  gap: clamp(1rem, 3vw, 2rem);
}

.tail__brand img {
  display: block;
  height: 58px; /* ajusta al alto de tu logo de referencia */
  width: auto;
}

/* Centro: Follow + redes */
.tail__social {
  display: flex;
  align-items: center;
  gap: clamp(0.6rem, 1.8vw, 1.2rem);
}

.follow__arrow {
  height: 86px;
  width: auto;
}

.follow__badge {
  height: 36px;
  width: auto;
}

.socials {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* Botones circulares */
.social {
  --size: 52px;
  width: var(--size);
  height: var(--size);
  border-radius: 999px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  color: #fac532;
  text-decoration: none;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.15);
  transition: transform 0.2s ease, box-shadow 0.2s ease, color 0.2s ease, background 0.2s ease;
}

.social:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.22);
  background: #3886c9;
  color: #ffffff;
}

/* Botón pill de privacidad */
.privacy-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  background: #111;
  color: #fff;
  padding: 0.7rem 1.2rem;
  border-radius: 999px;
  text-decoration: none;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
}

.privacy-btn:hover {
  transform: translateY(-2px);
  background: #000;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.32);
}

/* Línea inferior + dirección */
.tail__address {
  margin-top: clamp(1rem, 3vw, 1.6rem);
  padding-top: 0.8rem;
  border-top: 1px dashed rgba(255, 255, 255, 0.35);
  text-align: center;
  font-size: 0.9rem;
  letter-spacing: 0.06em;
  color: #e0eeff;
}

/* Responsive */
@media (max-width: 1100px) {
  .site-tail__row {
    grid-template-columns: 1fr;
    gap: 1.2rem;
    text-align: center;
  }
  .tail__social {
    justify-content: center;
    flex-wrap: wrap;
  }
  .privacy-btn {
    justify-self: center;
  }
  .tail__brand img {
    margin-inline: auto;
  }
}
.hero {
  position: relative;
}

.hero--cover {
  min-height: clamp(420px, 75vh, 680px);
}

.hero--cover::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 2;
  background: linear-gradient(90deg, #294e85, rgba(0, 0, 0, 0.05));
}

.hero__overlay {
  position: absolute;
  inset: 0;
}

.hero__content {
  position: relative;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: inherit;
}

.hero__content h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: clamp(2rem, 4.6vw, 3.5rem);
  line-height: 1.3;
  max-width: 29ch;
  text-shadow: 0 2px 18px rgba(0, 0, 0, 0.25);
}

/* ===== Slides de fondo ===== */
.hero__slides {
  position: absolute;
  inset: 0;
  z-index: 1;
  overflow: hidden;
}

.hero__slide {
  position: absolute;
  inset: 0;
  background: var(--img) center/cover no-repeat;
  opacity: 0;
  transform: scale(1.1); /* inicia un poco más grande para el fade-in si quieres */
  transition: opacity 1s ease, transform 6s ease; /* suavidad al entrar y efecto de zoom */
  will-change: opacity, transform;
}

/* Ken Burns para la slide activa */
@keyframes hero-kenburns {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.15);
  }
}
.hero__slide.is-active {
  opacity: 1;
  animation: hero-kenburns 7s ease-in-out both; /* “acercamiento” suave */
  transform: scale(1); /* punto de partida */
}

/* Accesibilidad: reduce motion */
@media (prefers-reduced-motion: reduce) {
  .hero__slide {
    transition: none;
    transform: none;
  }
  .hero__slide.is-active {
    animation: none;
  }
}
.pill-strip {
  z-index: 4;
  position: relative;
  margin-top: -3.2rem;
  margin-bottom: 1.5rem;
}

.pill {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 1.25rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  border-radius: 2.5rem;
  padding: 1rem 1.25rem;
  align-items: center;
}

.pill__item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 0.6rem 1rem;
}

.pill__icon {
  width: 48px;
  height: 48px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  background: #005AFF;
  color: #ffffff;
}

.pill__title {
  font-weight: 700;
  line-height: 1;
}

.pill__subtitle {
  color: #6b7280;
  font-size: 0.95rem;
  margin-top: 0.15rem;
}

@media (max-width: 900px) {
  .pill {
    grid-template-columns: 1fr;
    border-radius: 1.25rem;
  }
}
/* ===== About ===== */
.about.pad {
  padding-block: clamp(1rem, 2vw, 1.8rem);
}

.about__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(0.6rem, 1.5vw, 1rem); /* más cerrado */
  align-items: start;
}

.about__media {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  gap: 1rem;
}

/* Imagen alta a la izquierda, dos imágenes apiladas a la derecha */
.about__img {
  margin: 0;
  border-radius: 18px;
  overflow: hidden;
  background: #ddd;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}
.about__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__img--tall {
  grid-column: 1/2;
  grid-row: 1/3;
  min-height: clamp(320px, 48vh, 500px);
  overflow: hidden;
}

.about__img--tall img {
  transition: transform 0.6s cubic-bezier(0.25, 0.1, 0.25, 1);
  transform-origin: center center;
}

.about__img--tall:hover img {
  transform: scale(1.08);
}

.about__img--top {
  grid-column: 2/3;
  grid-row: 1/2;
  min-height: clamp(160px, 24vh, 230px);
}

.about__img--bottom {
  grid-column: 2/3;
  grid-row: 2/3;
  min-height: clamp(160px, 24vh, 230px);
}

/* Badge circular azul centrado entre imágenes */
.xp-badge {
  position: absolute;
  left: 48%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: clamp(120px, 16vw, 180px);
  height: clamp(120px, 16vw, 180px);
  border-radius: 999px;
  background: #005AFF; /* azul principal de tu marca */
  color: #ffffff;
  place-items: center;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.12), 0 6px 18px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  justify-content: center; /* centra ambos en el eje vertical */
  align-items: center;
  text-align: center;
}

.xp-badge__num {
  font-weight: 700;
  font-size: clamp(2rem, 4vw, 3rem);
}
.xp-badge__num span {
  font-weight: 700;
  margin-left: 0.1em;
}
.xp-badge__num {
  line-height: 1; /* evita el espacio vertical exagerado */
  margin-bottom: 0.2rem; /* ajusta la distancia manualmente */
}

.xp-badge__txt {
  font-size: clamp(0.8rem, 1.2vw, 0.95rem);
  opacity: 0.95;
  line-height: 1.1; /* más compacto */
  opacity: 0.95;
}

/* Columna derecha: título, línea divisoria y copy */
.about__content {
  /* línea vertical sutil como en la imagen */
  border-left: 2px solid rgba(0, 0, 0, 0.08);
  padding-left: clamp(0.6rem, 2vw, 1rem);
}

.about__content_R {
  /* línea vertical sutil como en la imagen */
  border-right: 2px solid rgba(0, 0, 0, 0.08);
  padding-right: clamp(0.6rem, 2vw, 1rem);
}

.about__content h2 {
  margin: 0 0 0.8rem 0;
  font-size: clamp(1.6rem, 3vw, 2.2rem);
  font-weight: 700;
}

.about__copy {
  display: grid;
  gap: none;
  color: #192231; /* texto oscuro legible */
  padding: 0%;
}

.about__cta {
  margin-top: 1.2rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 10px 24px rgba(17, 91, 240, 0.25); /* halo azul */
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.about__cta:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 30px rgba(17, 91, 240, 0.32);
}

/* Responsive */
@media (max-width: 1024px) {
  .about__grid {
    grid-template-columns: 1fr;
  }
  .about__content {
    border-left: 0;
    padding-left: 0;
  }
  .xp-badge {
    left: 50%;
    top: 50%;
  }
}
/* ===== Locations===== */
.loc {
  position: relative;
  color: #ffffff;
  overflow: hidden;
  /* espacio vertical moderado */
  padding-block: clamp(2rem, 6vw, 4rem);
