/* ===== Performance ===== */
.performance {
  /* Imagen de fondo por CSS custom prop */
  --bg: url("/build/img/perf-bg.webp");
  position: relative;
  isolation: isolate;
  background: #0b1f33; /* fallback */
  min-height: clamp(420px, 60vh, 640px);
  display: grid;
  align-items: center;
  /* capa de imagen */
}
.performance::before {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--bg) center/cover no-repeat;
  z-index: -2;
}
.performance {
  /* overlay para legibilidad */
}
.performance::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.55) 0%, rgba(0, 0, 0, 0.35) 40%, rgba(0, 0, 0, 0.25) 70%, rgba(0, 0, 0, 0.15) 100%);
  z-index: -1;
}

.performance__grid {
  display: grid;
  grid-template-columns: 1.4fr 0.9fr; /* texto más ancho, card a la derecha */
  gap: clamp(1.2rem, 4vw, 2.4rem);
  align-items: center;
}

.performance__copy {
  color: #ffffff;
  max-width: 62ch;
}

.performance__copy h2 {
  font-weight: 900;
  font-size: clamp(1.8rem, 3.5vw, 2.6rem);
  line-height: 1.15;
  margin: 0 0 0.8rem;
  text-shadow: 0 2px 18px rgba(0, 0, 0, 0.35);
}

.performance__copy p {
  margin-top: 0.8rem;
  font-size: clamp(1rem, 1.3vw, 1.15rem);
}

/* Bullets con puntos • grandes como en el mock */
.performance__bullets {
  list-style: none;
  padding: 0;
  margin: 0.6rem 0;
  display: grid;
  gap: 0.75rem;
  font-size: clamp(1rem, 1.35vw, 1.25rem);
}

.performance__bullets li {
  position: relative;
  padding-left: 1.2rem;
}

.performance__bullets li::before {
  content: "•";
  position: absolute;
  left: 0;
  top: 0;
  line-height: 1;
  color: #ffffff;
  opacity: 0.9;
  font-weight: 700;
}

/* Tarjeta lateral */
.performance__card {
  margin: 0;
  justify-self: end;
  width: min(520px, 90%);
  aspect-ratio: 16/9;
  border-radius: 22px;
  overflow: hidden;
