/* ===== Productivity ===== */
.kpi__grid {
  display: grid;
  grid-template-columns: 1.1fr 1fr; /* texto un poco más ancho */
  gap: clamp(1.5rem, 4vw, 3rem);
  align-items: center;
}

/* Texto */
.kpi__copy h2 {
  font-weight: 700;
  font-size: clamp(2.6rem, 4vw, 3.3rem);
  line-height: 1.15;
  margin: 0 0 0.8rem;
}

