  /* Capa: oscurecido suave */
}
.collections::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  z-index: -1;
}

.collections__inner {
  display: grid;
  grid-template-columns: minmax(0, 1.3fr) auto;
  gap: clamp(1.5rem, 5vw, 3rem);
  align-items: center;
}

/* Texto */
.collections__copy {
  max-width: 60ch;
}

.collections__copy h2 {
  margin: 0 0 1rem;
  font-weight: 900;
  font-size: clamp(1.9rem, 3.4vw, 2.6rem);
  line-height: 1.1;
}

.collections__copy p {
  font-size: clamp(1rem, 1.3vw, 1.15rem);
  line-height: 1.7;
}

/* Tarjeta de imagen derecha */
.collections__media {
  margin: 0;
  width: clamp(240px, 30vw, 360px);
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.35);
}

.collections__media img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Responsive */
@media (max-width: 900px) {
  .collections {
    text-align: left;
  }
  .collections__inner {
    grid-template-columns: 1fr;
  }
  .collections__media {
    justify-self: flex-start;
  }
}
/* ===== Sales ===== */
.sales {
  background: #f5f5f5;
}

/* TOP */
.sales__top {
  display: grid;
  grid-template-columns: minmax(0, 1.2fr) minmax(0, 1fr);
  gap: clamp(2rem, 5vw, 3rem);
  align-items: center;
  margin-bottom: clamp(2.5rem, 6vw, 3.5rem);
}

.sales__top-media {
  position: relative;
  max-width: 520px;
}

.sales__img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 1.25rem;
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.18);
}

.sales__img--small {
  position: absolute;
  right: clamp(1rem, 3vw, 1.5rem);
  bottom: clamp(-3rem, -4vw, -2.5rem);
  width: 46%;
  border-radius: 1.25rem;
}

/* Texto top */
.sales__top-copy h2 {
  margin: 0 0 1rem;
  font-size: clamp(2rem, 3.2vw, 2.6rem);
  font-weight: 700;
  text-align: center;
}

.sales__top-copy p {
  max-width: 34ch;
  margin: 0 auto;
  text-align: justify;
  font-size: clamp(1.2rem, 1.4vw, 1.4rem);
  line-height: 1.7;
}

/* BOTTOM */
.sales__bottom {
  display: grid;
  grid-template-columns: minmax(0, 1.1fr) auto;
  gap: clamp(2rem, 5vw, 3rem);
  align-items: center;
  margin-top: clamp(3rem, 6vw, 4rem);
}

.sales__bottom-copy h3 {
  margin: 0 0 0.5rem;
  font-size: clamp(1.4rem, 2vw, 1.6rem);
  font-weight: 700;
}

.sales__bottom-copy p {
  margin: 0 0 1.4rem;
  max-width: 44ch;
  font-size: clamp(1.2rem, 1.4vw, 1.4rem);
  line-height: 1.7;
}

.sales__note {
  text-align: center;
  font-weight: 700;
}

.sales__leaf {
  margin: 0;
  width: clamp(180px, 20vw, 340px); /* más estrecha */
  aspect-ratio: 0.75/1; /* más alta (relación vertical) */
  border-radius: 0 0 999px 999px;
  overflow: hidden;
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.25);
}

.sales__leaf img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Responsive */
@media (max-width: 900px) {
  .sales__top {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .sales__top-media {
    margin: 0 auto clamp(3rem, 8vw, 4rem);
  }
  .sales__top-copy p {
    max-width: 40ch;
  }
  .sales__bottom {
    grid-template-columns: 1fr;
  }
  .sales__leaf {
    justify-self: center;
  }
  .sales__bottom-copy {
    text-align: left;
  }
}
/* ===== Clients / Logos ===== */
.clients {
  position: relative;
  color: #000000;
  background: #f5f5f5;
  background-size: cover;
  background-position: center;
}

.clients__overlay {
  padding-block: clamp(3rem, 8vh, 5rem);
  backdrop-filter: blur(2px);
}

.clients__inner {
  text-align: center;
  max-width: 1000px;
  margin-inline: auto;
}

.clients__title {
  font-weight: 900;
  font-size: clamp(1.8rem, 3vw, 2.3rem);
  margin-bottom: 1rem;
}

.clients__text {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: clamp(2rem, 4vh, 2.5rem);
}

/* Carrusel */
.clients__carousel {
  position: relative;
  overflow: hidden;
  padding-block: 1.5rem;
}

.clients__track {
  display: flex;
  align-items: center;
  gap: clamp(2.5rem, 5vw, 4rem);
  width: max-content;
  animation: clients-slide 38s linear infinite;
  will-change: transform;
}

.clients__carousel:hover .clients__track {
  animation-play-state: paused;
}

.clients__logo {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-inline: 0.5rem;
}

.clients__logo img {
  max-height: 260px;
  width: auto;
  display: block;
  filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.35));
}

/* Animación de scroll continuo */
@keyframes clients-slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
/* Respeto a usuarios con reduce motion */
@media (prefers-reduced-motion: reduce) {
  .clients__track {
    animation: none;
  }
}
/* Barra inferior CTA */
.clients__cta {
  background: linear-gradient(90deg, #0b4eea, #0058ff);
  padding-block: 0.9rem;
}

.clients__cta-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  font-size: 0.95rem;
  color: #ffffff;
}

.clients__cta-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.45rem 1.1rem;
  border-radius: 999px;
  background: #ffffff;
  color: #0050dd;
  font-weight: 700;
  font-size: 0.9rem;
  text-decoration: none;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease, background 0.2s ease, color 0.2s ease;
}

.clients__cta-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.3);
  background: #001f66;
  color: #ffffff;
}

@media (max-width: 768px) {
  .clients__cta-inner {
    flex-direction: column;
  }
}
.pill_all_txt {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  border-radius: 2.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 1rem 1.25rem;
  text-align: center;
  font-weight: 400;
  font-size: 1.4rem;
  color: #6b7280;
}

/* ===== Case metrics (Over 2 years...) ===== */
.case-metrics {
  padding-block: clamp(3rem, 7vh, 4.5rem);
  background: linear-gradient(to bottom, #ffffff 0%, #fff7f0 100%);
}

.case-metrics__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: clamp(2rem, 5vw, 3.5rem);
}

.case-metrics__card {
  background: linear-gradient(90deg, #0b3e74, #052b52);
  color: #ffffff;
  border-radius: 2.2rem;
  padding: clamp(1.8rem, 3vw, 2.3rem);
  max-width: 460px;
  width: 100%;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.16);
}

.case-metrics__title {
  font-weight: 700;
  text-align: center;
  font-size: clamp(1.6rem, 2.2vw, 1.9rem);
  margin: 0 0 1rem;
}

.case-metrics__text {
  margin: 0 0 1.5rem;
  line-height: 1.6;
  font-size: 0.98rem;
}

.case-metrics__divider {
  display: block;
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed rgba(255, 255, 255, 0.4);
}

/* Responsive: en móviles se apilan */
@media (max-width: 768px) {
  .case-metrics__card {
    max-width: 100%;
  }
}
.hero__content h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: clamp(2rem, 4.6vw, 3.5rem);
  line-height: 1.3;
  max-width: 29ch;
  text-shadow: 0 2px 18px rgba(0, 0, 0, 0.25);
  /* 🔽 Animación */
  opacity: 0;
  transform: translateY(40px);
  animation: fadeUp 1.2s ease-out forwards;
  animation-delay: 0.4s;
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/* ===== Animación de entrada tipo "desde atrás" ===== */
.pill {
  opacity: 0;
  transform: scale(0.8);
  animation: zoomIn 1.2s cubic-bezier(0.22, 1, 0.36, 1) forwards;
  animation-delay: 1s; /* entra justo después del título */
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Opcional: efecto secuencial en cada pill__item */
.pill__item {
  opacity: 0;
  transform: translateZ(-100px);
  animation: appearForward 0.8s ease-out forwards;
}

.pill__item:nth-child(1) {
  animation-delay: 1.2s;
}

.pill__item:nth-child(2) {
  animation-delay: 1.4s;
}

.pill__item:nth-child(3) {
  animation-delay: 1.6s;
}

@keyframes appearForward {
  0% {
    opacity: 0;
    transform: translateZ(-100px) scale(0.9);
  }
  100% {
    opacity: 1;
    transform: translateZ(0) scale(1);
  }
}
/* Opcional: que se vean bien los números */
@property --badge-scale {
  syntax: "<number>";
  inherits: false;
  initial-value: 1;
}
.xp-badge__num {
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: antialiased;
}

/* Zoom al completar el conteo */
@keyframes badgePulse {
  0% {
    --badge-scale: 1;
  }
  50% {
    --badge-scale: 1.08;
  }
  100% {
    --badge-scale: 1;
  }
}
.xp-badge.pulse-zoom {
  animation: badgePulse 0.5s ease-out;
  will-change: transform;
}

/* Respeta usuarios con motion reducido */
@media (prefers-reduced-motion: reduce) {
  .xp-badge.pulse-zoom {
    animation: none;
  }
}
.Colum_left,
.Colum_right {
  opacity: 0;
  transform: translateX(0);
  transition: all 1s ease-out;
}

/* Posiciones iniciales */
.Colum_left {
  transform: translateX(-80px);
}

.Colum_right {
  transform: translateX(80px);
}

/* Cuando es visible */
.Colum_left.visible,
.Colum_right.visible {
  opacity: 1;
  transform: translateX(0);
}

.privacy h2 {
  font-weight: 700;
  font-size: 3.5rem;
  padding: 1.2rem;
}

.privacy h3 {
  font-weight: 700;
  font-size: 1.8rem;
}

.privacy p {
  font-weight: 400;
  color: #6b7280;
  text-align: justify;
  width: 70%;
}

.contact {
  position: relative;
  overflow: hidden;
  background: radial-gradient(1200px 600px at 25% -10%, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0) 60%), radial-gradient(900px 500px at 80% 30%, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0) 60%), linear-gradient(90deg, #0b3e74, #052b52);
}

.contact__title {
  color: #ffffff;
  font-weight: 700;
  font-size: clamp(1.6rem, 3.4vw, 2.4rem);
  margin: 0 0 1rem;
}

.form {
  display: grid;
  gap: 1rem;
  max-width: 560px;
  margin-inline: auto;
  color: #ffffff;
}

.form label {
  display: grid;
  gap: 0.35rem;
}

.form input, .form textarea {
  border: 1px solid #ccc;
  border-radius: 12px;
  padding: 0.85rem 1rem;
  font: inherit;
  outline: none;
  transition: 0.15s;
  background: #ffffff;
}

/* inputs estilo tarjeta clara como en la imagen */
.form label span {
  color: rgba(255, 255, 255, 0.9);
}

.form input,
.form textarea,
.form select {
  background: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.35);
}

.form input::placeholder,
.form textarea::placeholder,
.form select::placeholder {
  color: #7a8aa1;
}

.form input:focus,
.form textarea:focus,
.form select:focus {
  box-shadow: 0 0 0 6px rgba(17, 91, 240, 0.18);
  border-color: rgb(17, 91, 240);
}

.form select {
  border-radius: 6px;
  background-color: #ffffff;
  min-height: 4.5vh;
}

/* grid de los 2 primeros campos */
.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

@media (max-width: 768px) {
  .grid-2 {
    grid-template-columns: 1fr;
  }
}
/* checkbox legal y botón centrados */
.contact__legal {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.95rem;
  color: #e7eef9;
  justify-content: center;
}

.contact__legal a {
  color: #cfe2ff;
  text-decoration: underline;
  text-align: justify;
}

.contact__submit {
  display: block;
  margin: 0.75rem auto 0;
  padding-inline: 2.5rem;
  cursor: pointer;
}

/* opcional: sombra sutil al bloque de campos para levantarlo del fondo */
.contact__form > *:not(.contact__legal):not(.contact__submit) {
  filter: drop-shadow(0 6px 16px rgba(0, 0, 0, 0.08));
}